/*==========================
  RESET STYLING
==========================*/
body, html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/*==========================
  GLOBAL STYLES
==========================*/
body {
  background-color: black;
  font-family: sans-serif;
  color: white;
  padding-top: 100px; /* Adjust this if necessary based on header height */
}

/*==========================
  HEADER STYLES
==========================*/
.sticky-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #000000; /* Dark gray, very close to black */
  background-image: radial-gradient(ellipse at center, rgba(255, 255, 255, 0.2) 0%, transparent 65%);
  padding: 0 20px; /* Reduced padding for a shorter header */
  text-align: center;
  z-index: 1000;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5); /* Subtle shadow */
  height: auto;
}

/*==========================
  MAIN CONTENT STYLES
==========================*/
main {
  padding-top: 120px; /* Adjust this based on the final height of your header */
}

.title-text {
  font-family: "Pacifico", cursive;
  font-weight: 400;
  font-size: 60px;
  color: darkseagreen;
}

/*==========================
  CONTENT SECTIONS
==========================*/
.content-section {
  display: grid;
  grid-template-columns: 1fr;
  gap: 40px;
  margin: 20px;
  padding: 20px;
}

.s1, .projects-section, .contact-section {
  background-color: #1a1a1a;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.s1 h2, .projects-section h2, .contact-section h2 {
  color: #efefef;
  text-align: center;
}

/*==========================
  LIST STYLES
==========================*/
ul {
  margin: 0;
  padding: 0;
}

li {
  margin: 0;
  padding: 0;
}

/*==========================
  LINK STYLES
==========================*/
a {
  color: #03C682;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/*==========================
  PDF VIEWER STYLES
==========================*/
.pdf-viewer {
  position: relative;
  overflow: hidden;
  background-color: #1a1a1a; /* Background for the viewer */
  padding: 5px;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  margin: 0px 0; /* Add margin for spacing */
}

.pdf-viewer iframe {
  width: 100%;
  height: 600px; 
  outline: none; /* Removes outline */
  border: none; /* Removes border */
  display: block; /* Ensures no extra space below */
}

.dark-pdf {
  filter: invert(1) hue-rotate(180deg); /* Invert colors for dark mode */
}

.light-pdf {
  filter: none; /* Default styling for light mode */
}

/*==========================
  MEDIA QUERIES
==========================*/
 @media screen and (min-width: 768px) { 
  .content-section {
    grid-template-columns: 1fr 1fr; /*For larger screens, use a two-column layout */
  }
} 

/*==========================
  HOME CONTENT STYLES
==========================*/
.home-content {
  display: flex;
  justify-content: space-between; /* Space out the sections */
  margin-top: 10px; /* Space from the tabs */
}

.about-me, .useful-links, .resume {
  flex: 1; /* Each section takes equal width */
  margin: 0 10px; /* Spacing between sections */
  background-color: #1a1a1a; /* Background for sections */
  padding: 20px 20px; /* Increased padding for better spacing */
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

/*==========================
  CONTACT SECTION STYLES
==========================*/
.contact-section {
  flex: 1; /* Maintain equal width */
  margin: 20px 10px 20px; /* Increased margin-top for more space above */
  background-color: #1a1a1a; /* Background for section */
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

/*==========================
  TABS STYLES
==========================*/
.tabs {
  display: flex;
  justify-content: center;
  position: fixed; /* Fix the tabs at a specific position */
  top: 170px; /* Adjust this based on your header height */
  left: 0; /* Align with the left edge */
  right: 0; /* Align with the right edge */
  background-color: #000000; /* Dark gray, very close to black */
  z-index: 1000; /* Ensure it is above other content */
  padding: 10px 0; /* Padding for the tabs */
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5); /* Subtle shadow */
}

.tabs button {
  margin: 0 10px;
  padding: 10px 20px;
  background-color: #1a1a1a;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.tabs button.active {
  background-color: #03C682; /* Highlight color for active tab */
  color: black; /* Active tab text color */
}

.tabs-container {
  text-align: center; /* Center the tabs */
  margin-top: 10px; /* Adjust based on the header height */
}

/*==========================
  HEADING STYLES
==========================*/
h2 {
  margin: 0;
  padding: 0;
  line-height: 1.2;
}

/*==========================
  CURLY BRACKET STYLES
==========================*/
.curly-bracket {
  font-size: 24px; /* Adjust size */
  color: darkseagreen; /* Bracket color */
  margin: 0 5px; /* Space around brackets */
  vertical-align: middle; /* Align with text */
}

/*==========================
  FOOTER
==========================*/
.footer {
  background-color: #000000; /* Matches the dark theme */
  padding: 0.5rem 0; /* Reduced padding for a thinner footer */
  border-top: 1px solid rgba(255, 255, 255, 0.2); /* Lighter border for contrast */
  position: fixed;
  width: 100%;
  bottom: 0;
}

.footer-content {
  text-align: center;
  color: #ffffff; /* White text for readability */
}

.footer-content p {
  margin: 0;
  font-size: 0.8rem; /* Reduced font size for a more compact appearance */
}

.social-links {
  margin-top: 0.25rem; /* Reduced margin for social links */
}

.social-links a {
  margin: 0 0.5rem;
  text-decoration: none;
  color: #03C682; /* Keep link color as is, or adjust to match your theme */
}

.social-links a:hover {
  text-decoration: underline; /* Keeps hover effect for clarity */
  color: rgba(255, 255, 255, 0.7); /* Optional: change hover color for better contrast */
}
